@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700);
:root {
  --main-bg-color: rgb(219, 80, 74);
}
body {
  background: var(--main-bg-color);
  font-family: 'Open Sans', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,  'Helvetica Neue', sans-serif;
  margin: 0;
  -webkit-transition: 0.17s all ease-in-out;
  transition: 0.17s all ease-in-out;
}
.container {
  max-width: 1170px;
  margin: 0 auto;
}
img {
  -webkit-user-drag: none;
  -moz-user-drag: none;
  -khtml-user-drag: none;
}

.header {
  display: grid;
  grid-template-columns: auto auto;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 10px;
}
.header .logo {
    width: 100px;
    position: relative;
    z-index: 5;
}
.header .link {
    color: white;
    text-decoration: none;
    font-weight: 300;
    text-align: right;
}
.main-content {
  margin-top: 10%;
  text-align: center;
}
.main-content .msg-box {
    border: 2px dashed rgba(0, 0, 0, 0.2);
    padding: 40px 20px;
    border-radius: 3px;
    width: 100%;
    max-width: 60%;
    margin: 0 auto;
    text-align: center;
    height: 60px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.main-content .msg-box span {
      color: white;
      font-size: 25px;
      font-weight: 300;
}
.main-content .copy-text {
    color: rgba(0, 0, 0, 0.3);
    text-align: center;
    margin-top: 20px;
    cursor: pointer;
}
.main-content .white-btn {
    background: white;
    padding: 12px 20px;
    font-size: 22px;
    color: var(--main-bg-color);
    font-weight: 300;
    margin-top: 50px;
    border-radius: 3px;
    cursor: pointer;
    outline: none;
    min-width: 40%;
    border: 0;
    -webkit-transition: 0.17s all ease-in-out;
    transition: 0.17s all ease-in-out;
}
.main-content .white-btn:active {
      -webkit-box-shadow: inset 0px 0px 2px 0px #787878;
              box-shadow: inset 0px 0px 2px 0px #787878;
}
.main-content .link {
    display: block;
    color: white;
    text-decoration: none;
    font-weight: 300;
    text-align: center;
    margin-top: 50px;
}
.footer {
  bottom: 10px;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr 3.5fr 1fr;
}
.footer .left-content, .footer .right-content {
    text-align: center;
}
.footer .left-content .hd1, .footer .right-content .hd1 {
      color: white;
      font-weight: 300;
      margin-bottom: 15px;
}
.footer .left-content .foo-logo1, .footer .right-content .foo-logo1 {
      margin-right: 10px;
}
.footer .right-content .foo-social {
    width: 50px;
}
.footer .foo-logo,
  .footer .foo-social {
    margin-right: 10px;
    cursor: pointer;
}
@media screen and (min-width: 768px) {
.link.add-reason2 {
    display: none;
}
}
@media screen and (max-width: 768px) {
.add-reason {
    display: none;
}
.header {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.main-content {
    margin-top: 15%;
}
.main-content .msg-box {
      max-width: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 85%;
      height: 80px;
      padding: 20px;
}
.main-content .msg-box span {
        font-size: 18px;
}
.main-content .white-btn {
      font-size: 18px;
      margin-top: 30px;
}
.footer {
    grid-template-columns: auto auto auto;
    margin-top: 30px;
    width: 100%;
    left: 0;
    position: static;
}
.footer .left-content .hd1, .footer .right-content .hd1 {
      font-size: 12px;
}
.footer .right-content .foo-social {
      margin-right: 3px;
      width: 40px;
}
.footer .foo-logo {
      margin-right: 3px;
      width: 40px;
}
}
.bounce {
  -webkit-animation: bounce 3s infinite;
  animation: bounce 3s infinite;
}
@-webkit-keyframes bounce {
0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
}
40% {
    -webkit-transform: translateY(-20px);
    transform: translateY(-20px);
}
60% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
}
}
@keyframes bounce {
0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
}
40% {
    -webkit-transform: translateY(-20px);
    transform: translateY(-20px);
}
60% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
}
}

